import React from 'react';
import Preloader from '../../common/Preloader/Preloader';
import style from './ProfileInfo.module.css';
import jobStatusOn from '../../../assets/images/red-v.png';
import jobStatusOff from '../../../assets/images/red-x.png';
import SocialNetwork from '../../common/SocialNetworck/SocialNetwork';
import ProfileStatusWithHooks from './ProfileStatusWithHooks';


const ProfileInfo = ({profile, status, updateStatus}) => {

    if(!profile){
        return <Preloader />
    }

    let lookingStatus = (profile.lookingForAJob === true ? jobStatusOn : jobStatusOff);

    return (
        <div>
           {/*  <div>
                <img src='https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTU1dFR0DR4d0wILeQRaH1oo1Dlsag-Gp2dXg&usqp=CAU'></img>
            </div> */}
            <div className={style.descriptionBlock}>
                <img src={profile.photos.large}/>
            </div>
            <ProfileStatusWithHooks status={status} 
                                    updateStatus={updateStatus}/>
            <div>{profile.fullName}</div>
            <div>{profile.aboutMe}</div>
            <div>Статус поиска работы: <img src={lookingStatus} /> </div>
            <div><SocialNetwork contacts={profile.contacts}/></div>
        </div>
    )
}

export default ProfileInfo;