import React from 'react';
import facebook from '../../../assets/images/Social_Icons_v3_(graphicsland.ru)/png 500px/Facebook.png'

let SocialNetwork = (props) => {

    let socialNetworks = Object.keys(props.contacts).map((key) => {
        if(props.contacts[key] !== null)
            return ` ${props.contacts[key]} ---`;
    })

    return <div>
           --- {socialNetworks}
    </div>
}

export default SocialNetwork;